.hamburger {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	@include sm {
		width: 20px; }
	&:focus {
		outline: none; }
	&-box {
		@include sm {
			width: 20px; } }
	&-inner {
		@include sm {
			width: 20px; }
		&:before {
			@include sm {
				width: 20px; } }
		&:after {
			@include sm {
				width: 20px; } } }
	&:not(.is-active) {
		.hamburger {
			&-box {}
			&-inner {
				@include min_sm {
					top: 1px !important; }
				&:before {
					@include min_sm {
						top: 7px !important; } }
				&:after {
					width: 60%;
					@include min_sm {
						top: 14px !important; } } } } } }
