.penalty-table {
	padding-bottom: 7.5rem;
	@include min_sm {
		background: $c4; }
	@include sm {
		padding-bottom: 0; }

	&__row {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		@include min_lg {
			margin: 0 -10px; } }

	&__col {
		@include min_lg {
			padding: 0 10px; }
		&--left {
			flex-basis: 69.181%;
			max-width: 69.181%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } }
		&--right {
			flex-basis: 30.819%;
			max-width: 30.819%;
			@include lg {
				display: none; } } }

	&__popular-penalties {
		margin-bottom: 20px; }

	&__icons {
		@include min_sm {
			margin-bottom: 20px; } }

	&__scrolling {
		@include min_lg {
			max-height: calc( 100vh - 20px);
			overflow-y: auto;
			overflow-x: hidden; }

		&-wrap {
			position: relative;
			max-width: 319px;
			@include xl {
				max-width: 272.77px; }
			@include lg {
				max-width: 100%; }
			@include min_lg {
				overflow: hidden;
				width: 100%;
				max-height: calc( 100vh - 20px); }
			&--scrolled {
				@include min_lg {
					position: fixed;
					top: 20px; } } } }

	&__popular-penalties-wrap {
		padding-bottom: 20px; } }
