.addresses {
	padding: 4.625rem 0 5rem;
	@include sm {
		padding: 30px 0; }

	&__title {
		margin-bottom: 10px;
		@include sm {
			margin-bottom: 15px; } }

	&__desc {
		max-width: 920px;
		line-height: 24px;
		margin-bottom: 2.4375rem;
		p {
			@include sm {
				margin-bottom: 0; } }
		@include lg {
			line-height: 22px; }
		@include md {
			line-height: 20px; }
		@include sm {
			margin-bottom: 20px;
			line-height: 18px; } } }

.gray-card {
	background: linear-gradient(180deg, $c4 0%, #E5E7EB 100%), $c4;
	border-radius: 3px;
	padding: 30px;
	@include lg {
		padding: 25px; }
	@include md {
		padding: 20px; }
	@include sm {
		padding: 15px; }


	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -7.5px; }

	&__col {
		padding: 0 7.5px;
		&:first-child {
			flex-basis: 30.73%;
			max-width: 30.73%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%;
				margin-bottom: 15px; } }
		&:last-child {
			flex-basis: 69.27%;
			max-width: 69.27%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } } } }

.feedback-info {
	background: #f7f7f8;
	padding: 1.875rem 1.5625rem 2.8125rem 1.8125rem;
	margin-bottom: 10px;
	@include lg {
		margin-bottom: 20px; }
	@include md {
		padding: 20px; }

	&__title {
		font-weight: 500;
		font-size: 20px;
		line-height: 30px;
		color: #343D52;
		margin-bottom: 37px;
		@include sm {
			font-weight: 500;
			font-size: 16px;
			line-height: 21px;
			color: $c0; } }

	&__row {
		display: flex;
		justify-content: space-between;
		@include lg {
			flex-wrap: wrap; } }

	&__col {
		&:not(:last-child) {
			@include lg {
				border-bottom: 1px solid #DCDDE0;
				margin-bottom: 20px; } }
		&:first-child {
			flex-basis: 33%;
			max-width: 33%;
			@include xl {
				flex-basis: 40%;
				max-width: 40%; }
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } }
		&:nth-child(2) {
			flex-basis: 27.8%;
			max-width: 27.8%;
			@include xl {
				flex-basis: 25%;
				max-width: 25%; }
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } }
		&:nth-child(3) {
			flex-basis: 30.41%;
			max-width: 30.41%;
			@include xl {
				flex-basis: 32%;
				max-width: 32%; }
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } } }

	&__geo-wrap {
		display: flex;
		margin-bottom: 24px;
		@include lg {
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid #DCDDE0; } }


	&__geo-icon {
		display: flex;
		justify-content: center;
		margin-right: 9px;
		margin-top: 5px;
		svg {
			width: 14px;
			height: 14px;
			fill: $c3; } }

	&__geo-title-wrap {}

	&__small-title {
		line-height: 21px;
		margin-bottom: 5px; }

	&__link {} }


.days {
	display: flex;
	@include lg {
		margin-bottom: 20px; }
	&__icon-wrap {
		display: flex;
		justify-content: center;
		margin-right: 8px;
		margin-top: 2px;
		svg {
			width: 16px;
			height: 16px;
			fill: $c3; } }

	&__title-wrap {}

	&__title {
		line-height: 21px;
		&:not(:last-child) {
			margin-bottom: 2px; }
		span {
			color: $c3; } }

	&__desc {}

	&__title {} }


.author {
	display: flex;
	@include lg {
		margin-bottom: 20px; }
	@include sm {
		flex-wrap: wrap;
		margin-bottom: 0; }

	&__img-wrap {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 7.25rem;
		height: 9.9375rem;
		min-width: 7.25rem;
		min-height: 9.9375rem;
		margin-right: 20px;
		@include lg {
			margin-right: 18px; }
		@include md {
			margin-right: 16px; }
		@include sm {
			width: 64px;
			height: 88px;
			min-width: 64px;
			min-height: 88px;
			margin-right: 15px;
			margin-right: 0; } }
	&__right-content {
		@include sm {
			flex-basis: 78.6%;
			max-width: 78.6%;
			padding-left: 15px; }
		@include xsm {
			flex-basis: 70%;
			max-width: 70%; } }

	&__img {
		width: 100%;
		height: 100%;
		object-fit: cover; }

	&__title {
		line-height: 21px;
		margin-bottom: 6px;
		@include sm {
			font-size: 14px;
			margin-bottom: 0; } }

	&__rank {
		margin-bottom: 16px; }

	&__small-title {
		line-height: 18px;
		flex-basis: 100%;
		max-width: 100%;
		margin-top: 15px; } }

