////////// Popup
.mfp {
	position: relative;
	max-width: 40.625rem;
	min-height: 26.25rem;
	text-align: left;
	margin: 2.5rem auto;
	border-radius: 3px;
	background: #fff;
	padding: 50px 50px 48px 50px;
	box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);

	.mfp-close {
		top: 20px;
		right: 20px;
		opacity: 1;
		transition: all .2s ease;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0;
		width: 16px;
		height: 16px;
		svg {
			pointer-events: none;
			width: 100%;
			height: 100%;
			fill: $c3; }
		&:focus {
			outline: none; }
		&:active {
			transform: translateY(1px); } } }

.form-result {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	background: #fff;
	opacity: 0;
	padding: 50px;
	visibility: hidden;
	transition: all .3s ease;
	z-index: 1;
	.mess {
		&__title {
			font-size: 42px;
			line-height: 47px;
			font-weight: 600;
			margin-bottom: 10px;
			opacity: 0;
			transform: translateY(50px);
			transition: all .6s ease;
			@include sm {
				font-size: 30px; }
			@include xsm {
				font-size: 25px;
				margin-bottom: 5px; } }

		&__desc {
			font-size: 22px;
			line-height: 27px;
			opacity: 0;
			transform: translateY(50px);
			transition: all .6s ease;
			@include xsm {
				font-size: 16px; } } }

	// &--success
	&--success {
		opacity: 1;
		visibility: visible;
		.mess__title,
		.mess__desc {
			opacity: 1;
			transform: translateY(0);
			transition-delay: .1s; }
		.mess__title {
			transition-delay: .2s; }
		.mess__desc {
			transition-delay: .3s; } } }

.answer-popup {
	position: absolute;
	top: 10px;
	right: -10px;
	width: 376px;
	background: #fff;
	border-left: 3px solid #F8C608;
	box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
	display: flex;
	justify-content: space-between;
	transform: translateX(100%);
	transition: all 0.3s ease;
	visibility: hidden;
	opacity: 0;
	z-index: 1;
	pointer-events: none;
	@include lg {
		width: 225px;
		transform: translateX(0);
		border-left: 0;
		background: $c0;
		border-radius: 3px;
		right: -2px;
		top: 46px; }
	&:after {
		content: '';
		position: absolute;
		top: 11px;
		left: -9px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 4.5px 6px 4.5px 0;
		border-color: transparent #f8c808 transparent transparent;
		@include lg {
			border-color: transparent transparent $c0 transparent;
			border-width: 0 8px 8px 8px;
			top: -7px;
			left: auto;
			right: 16px; } }
	&--small {
		border-radius: 3px;
		box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.1);
		width: 318px;
		border-left: none;
		padding: 20px;
		flex-direction: column;
		top: -24px;
		right: -30px;
		color: $c3;
		&.bottom {
			right: 97px !important;
			width: 295px !important;
			top: 29px;
			&::after {
				left: 100px !important;
				transform: rotate(90deg) !important;
				top: -20px !important; }
			@include lg {
				right: -200px !important; } }
		.answer-popup__desc {
			margin-bottom: 0; }
		.answer-popup__title {
			padding-top: 0;
			margin-bottom: 15px;
			font-size: 16px;
			font-weight: bold;
			color: $c0;
			line-height: 19px; }
		.answer-popup__desc {
			font-size: 12px;
			line-height: 18px;
			@include lg {
				color: $c3;
				text-align: left; } }
		&:after {
			left: -12px;
			top: 15px;
			border-width: 14.5px 12px 14.5px 0;
			border-color: transparent #fff transparent transparent; }
		@include lg {
			background: #fff;
			top: 30px;
			right: -40px;
			&:after {
				left: 265px !important;
				top: -20px;
				transform: rotate(90deg); } }
		@include md {
			right: -177px;
			&::after {} } }


	&__left-col {
		flex-basis: 230px;
		max-width: 230px;
		padding-right: 26px;
		padding-left: 18px;
		padding: 0 23px 0 18px;
		@include lg {
			flex-basis: 100%;
			max-width: 100%;
			padding: 11px 8px; } }

	&__right-col {
		flex-basis: 143px;
		max-width: 143px;
		min-width: 143px;
		@include lg {
			display: none; } }

	&__top-line {
		display: flex;
		align-items: center;
		padding-top: 17px;
		margin-bottom: 12px;
		@include lg {
			display: none; } }

	&__warning-icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 24px;
		height: 24px;
		min-width: 24px;
		min-height: 24px;
		margin-right: 12px; }


	&__desc {
		margin-bottom: 28px;
		@include lg {
			font-size: 12px;
			line-height: 17px;
			text-align: center;
			color: #FFFFFF;
			margin-bottom: 0;
			br {
				display: none; } } }

	&__small-title {
		display: flex;
		align-items: center;
		font-size: 12px;
		font-style: italic;
		line-height: 19px;
		color: $c0;
		@include lg {
			display: none; }

		span {
			font-size: 15px;
			line-height: 17px;
			color: $c3;
			margin-left: 4px; } }

	&__img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		&-wrap {
			left: 143px;
			height: 188px;
			display: flex;
			justify-content: center;
			align-items: center;
			@include lg {
				display: none; } } } }

.car-checker-popup {
