.owl-prev,
.owl-next {
	width: 42px;
	height: 42px;
	background: #ffffff !important;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.5s ease;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	svg {
		width: 10px;
		height: 12px;
		fill: $c0;
		transition: all 0.5s ease;
		@include sm {
			width: 8px;
			height: 10px; } }
	&:hover,
	&:focus {
		background: $accent !important;
		outline: none;
		svg {
			fill: #fff; } } }


.owl-prev {
	left: 30px;
	@include sm {
		left: 20px; }
	svg {
		transform: rotate(180deg); } }

.owl-next {
	right: 30px;
	@include sm {
		right: 20px; } }


