.signup-popup {
	.signup-popup__sub-title {
		display: flex;
		margin: 0 auto;
		margin-bottom: 40px;
		color: $c3;
		max-width: 420px; }

	&__title-wrap {
		width: 100% !important;
		// margin-top: 8px !important
 }		// max-height: 97px

	&__prev-btn {
		display: flex;
		align-items: center;
		font-size: 16px;
		line-height: 17px;
		color: $c3;

		&:hover {
			color: $c3;
			.signup-popup {
				&__prev-btn-icon-wrap {
					transform: translateX(-3px); } } }

		&-wrap {
			width: 100% !important;
			min-height: 9.4375rem;
