		flex-direction: column;
		align-items: center;
		margin-bottom: 45px !important;
		width: 100% !important;
		@include sm {
			margin-bottom: 30px !important; } }


	&__link {
		&--disabled {
			pointer-events: none;
			opacity: 0.5; } }

	&__btn {
		margin-bottom: 25px;
		.icon {
			transform: rotate(-90deg); } }

	&__next-wrap {
		width: 100% !important;
		display: flex;
		justify-content: center; }

	&__submit-wrap {
		width: 100% !important;
		display: flex;
		justify-content: center; }

	&__agreement {
		width: 100% !important;
		@include xxl {
			flex-direction: column;
			align-items: flex-start !important; }
		.agreement {
			&__btn {
				@include xxl {
					order: -1;
					margin-bottom: 15px;
					width: 100%; } } }
		.checkbox__text {
				br {
					@include xxl {
						display: none; } } } }


	.input--disabled {
		color: #CFCFCF !important; }
	.owl {
		&-nav {
			display: none; }
		&-dots {
			display: none; }
		&-item {
			visibility: hidden;
			&.active {
				visibility: visible; } } }


	&__dots {
		display: flex;
		justify-content: center;
		&[data-step="1"] {
			.signup-popup {
				&__dot {
