						background: #9C9C9B; } } } }
		&[data-step="3"] {
			.signup-popup {
				&__dot {
					&:nth-child(3) {
						background: #9C9C9B; } } } } }


	&__dot {
		width: 10px;
		height: 10px;
		min-width: 10px;
		min-height: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		background: #DBDBDB;
		border-radius: 50%;
		padding: 0;
		border: 0;
		transition: all 0.5s ease;
		pointer-events: none;
		&:focus {
			outline: none; }
		&:not(:last-child) {
			margin-right: 11px; } } }

.timeline {
	position: relative;
	margin-bottom: 38px;
	@include md {
		margin-bottom: 30px; }
	&[data-step="1"] {
		.timeline {
			&__item--step-1 {
				opacity: 1;
				display: flex; } } }
	&[data-step="2"] {
		.timeline {
			&__item--step-2 {
				opacity: 1;
				display: flex; } } }
	&[data-step="3"] {
		.timeline {
			&__item--step-3 {
				opacity: 1;
				display: flex; } } }

	&__item {
		display: flex;
		align-items: center;
		opacity: 0;
		transition: all 0.3s ease;
		display: none;
		&--step-3 {
			@include xxl {
				align-items: flex-start;
				.timeline {
					&__number {
						margin-top: 10px; } } } } }


	&__number {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 28px;
		height: 28px;
		min-width: 28px;
		min-height: 28px;
		margin-right: 15px;
		font-weight: bold;
		font-size: 16px;
		line-height: 21px;
		color: #fff;
		background: #009335;
		border-radius: 50%; }

	&__text {
		font-weight: 500;
		font-size: 16px;
		line-height: 21px;
		color: $c0;
		span {
			color: $c1; } } }

.benefits-list {
	padding-left: 17px;
	&__item {
		font-size: 14px;
		line-height: 17px;
		color: #009335;
