 }	// 	background: rgba($accent, 0.09)






.download-link {
	font-size: 16px;
	line-height: 17px;
	color: $accent;
	display: flex;
	align-items: center;
	&:hover {
		color: #2969EC;
		text-decoration: underline; }
	&__text {
		margin-right: 14px; }
	&__icon {
		&-wrap {
			width: 21px;
			height: 21px;
			min-width: 21px;
			min-height: 21px;
			display: flex;
			align-items: center;
			justify-content: center; } } }

.request-link {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: 500;
	font-size: 16px;
	line-height: 17px;
	color: $accent;
	padding: 13.5px 21px;
	background: #FFFFFF;
	border: 1px dashed $accent;
	border-radius: 3px;
	&:hover {
		color: #2969EC;
		border-color: #2969EC;
		.request-link {
			&__icon-wrap {
				transform: translateY(-3px);
				svg {
					fill: #2969EC; } } } }
	&__text {
		margin-right: 9px; }

	&__icon-wrap {
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3s ease;
		.icon {
			width: 12px;
			height: 14px;
			fill: $accent; } } }


