.advantages {
	background: $c4;
	padding: 4.75rem 0 3.25rem;
	@include sm {
		padding: 30px 0 23px; }

	&__title {
		margin-bottom: 2.25rem; }

	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -20px; }

	&-list {
		@include min_xl {
			margin-bottom: 30px; } }
	&__col {
		padding: 0 20px;
		&--left {
			flex-basis: 46.5%;
			max-width: 46.5%;
			display: flex;
			@include lg {
				flex-basis: 100%;
				max-width: 100%;
				order: 1; } }
		&--right {
			flex-basis: 53.5%;
			max-width: 53.5%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%;
				order: 0;
				margin-bottom: 30px; } } }

	&__report-wrap {
		background: #fff;
		transition: all 0.3s ease;
		width: 100%;
		height: 100%;
		@include min_xl {
			padding-top: 6px; }
		@include min_sm {
			padding-bottom: 30px; }
		@include sm {
			background: $c4; }
		&:hover {
			@include min_sm {
				box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.1); } } }


	&__img-wrap {
		display: flex;
		justify-content: center;
		margin-bottom: 2.4375rem;
		@include xl {
			margin-bottom: 30px; }
		@include sm {
			background: #fff;
			padding-bottom: 20px;
			box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.1); } }

	&__btn-wrap {
		display: flex;
		align-items: center;
		justify-content: center;
		@include xl {
			justify-content: space-between;
			margin-bottom: 30px; }
		@include sm {
			flex-direction: column; } }
	&__bottom-content {
		@include xl {
			padding: 0 15px; } }

	&__download-link {
		@include min_xl {
			margin-right: 3.75rem; }
		@include xl {
			@include min_sm {
				margin-right: 25px; } }
		@include sm {
			margin-bottom: 20px; } }


	&__request-link {
		@include sm {
			width: 100%; } } }
