 }		// 		border-color: $accent
	// &__not-found
	// 	position: relative
	// 	font-size: 16px
	// 	line-height: 16px
	// 	color: $c0
	// 	background: #fff
	// 	margin-bottom: -6px
	// 	margin-top: 20px
	// 	z-index: 1

	&__dropdown {
		width: 100%;
		background: #fff;
		top: 45px !important;
		border-radius: 3px;
		transition: all 0.3s ease;
		border: 1px solid $c3;
		border-top: 0;
		border-top-color: transparent;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
		overflow: hidden;
		max-width: 100%;
		ul {
			background: #fff;
			padding: 0;
			max-height: 307px;
			// margin-right: -17px
			// width: 318px
			width: calc( 100% + 17px); }
		li {
			padding: 14.5px 16px;
			font-size: 16px;
			line-height: 16px;
			color: $c0;
			cursor: pointer;
			transition: all 0.3s ease;
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
			max-width: 100%;
			&:hover {
				background: rgba($accent, .1);
				color: $accent; } } } }
.baron {
	&__scroll-bar {
		.baron {
			&__bar {
				display: block; }
			&__track {
				display: block; } } }

	&__bar {
		background: $c3;
		border-radius: 2px;
		width: 4px;
		right: 3px;
		cursor: pointer;
		&:hover {
			background: lighten($c3, 10%); } } }

select {
	&.error {
		+ .jq-selectbox {
			&__select {
				border-color: $c1 !important;
				background: #FFFAFA !important; } } }
	&.valid {
		+ .jq-selectbox {
			&__select {
				background: #F8FDFB !important;
				border-color: #36AB80 !important; } } } }


.select {
	position: relative;
	&--big {
		.jq-selectbox {
			&__select {
				padding: 16px 24px 16px 16px;
				@include lg {
					padding: 14px 24px 14px 16px; } } } }
	&--number {
		select {
			&.error {
				+ .jq-selectbox {
						&__select {
							border-color: $c3 !important;
							background: #FFFFFF !important; } } }
			&.valid {
				+ .jq-selectbox {
						&__select {
							background: #FFFFFF !important;
							border-color: $c3 !important; } } } }
		.jq-selectbox {
			&__select {
				padding: 8px 35px 8px 9px; }
			&__dropdown {
				top: 33px !important;
				li {
					padding: 14.5px 16px 14.5px 9px; } } } }

	&__title {
		font-weight: normal;
		margin-bottom: 8px;
		sup {
			top: 0;
			color: $c1;
			margin-left: 2px;
			font-size: 100%; } }

	&__sub-title {
		font-size: 12px;
		line-height: 17px;
		color: $c3;
		margin-bottom: 10px; } }

.search-list-popup {
	&-wrap {
		overflow: hidden;
		height: 100%;
		width: 100%; } }
