.main {
	&--car-checker {
		padding-top: 0;
		padding-bottom: 3.75rem;
		@include lg {
			padding-bottom: 0; }
		@include md {
			@include min_sm {
				padding-top: 30px; } }
		@include sm {
			background: #fff; }
		.main {
			&__top-line {
				position: relative;
				padding: 50px 66px 26px 64px;
				border-bottom: 1px solid #E4E5E8;
				@include lg {
					padding: 40px 40px 30px; }
				@include md {
					padding: 40px 25px 30px; }
				@include sm {
					padding: 30px 0;
					border-bottom: 0; }
				&:after {
					content: '';
					position: absolute;
					bottom: -8px;
					right: 0;
					width: 171px;
					height: 343px;
					background: url('../img/icons/big-eagle.png') no-repeat center center;
					background-size: 100% 100%;
					@include sm {
						display: none; } } }
			&__container {
				@include sm {
					padding: 0 0 0 15px; } } } } }
