.sites-card {
	display: flex;
	background: #FFFFFF;
	border: 1px solid $c3;
	border-radius: 3px;
	padding: 39px 40px 36px 39px;
	@include xl {
		padding: 29px 20px 25px 20px; }
	@include lg {
		flex-wrap: wrap; }
	@include md {
		flex-wrap: nowrap; }
	@include sm {
		flex-wrap: wrap;
		padding: 16px 19px 16px 19px; }

	&--vertical {
		flex-direction: column;
		align-items: flex-start;
		height: 100%;
		padding: 24px 30px 28px 37px;
		@include md {
			padding: 24px 15px 25px 15px; }

		.sites-card {
			&__img {
				height: auto;
				&-wrap {
					display: flex;
					justify-content: flex-start;
					align-items: center;
					width: auto;
					height: auto;
					min-width: auto;
					min-height: 43px;
					margin-right: 30px;
					@include md {
						margin-right: 15px; }
					@include sm {
						margin-right: 20px; } } }

			&__top-line {
				display: flex;
				align-items: center;
				margin-bottom: 25px; }

			&__title {
				margin-bottom: 0;
				line-height: 24px;
				@include md {
					@include min_sm {
						font-size: 18px; } } }
			&__desc {
				margin-bottom: 19px;
				br {
					@include xl {
						display: none; } } } } }

	&__col {
		&:first-child {
			@include lg {
				display: flex;
				align-items: center;
				margin-bottom: 15px; }
			@include md {
				align-items: flex-start; }
			@include sm {
				align-items: center; } }
		@include lg {
			@include min_md {
				flex-basis: 100%;
				max-width: 100%; } }
		@include sm {
			flex-basis: 100%;
			max-width: 100%; } }

	&__img {
		height: 100%;
		object-fit: cover;
		&-wrap {
			width: 5.8125rem;
			height: 5.625rem;
			min-width: 5.8125rem;
			min-height: 5.625rem;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 2.5rem;
			@include sm {
				width: 45px;
				height: 45px;
				min-width: 45px;
				min-height: 45px;
				margin-right: 15px; } } }

	&__title {
		font-weight: 500;
		font-size: 20px;
		line-height: 30px;
		color: $c0;
		margin-bottom: 12px;
		@include lg {
			line-height: 28px;
			margin-bottom: 0; }
		@include md {
			margin-bottom: 12px; }
		@include sm {
			margin-bottom: 0; }
		&--mobile {
			display: none;
			@include lg {
				display: block; }
			@include md {
				display: none; }
			@include sm {
				display: block; } }
		&--desktop {
			display: block;
			@include lg {
				display: none; }
			@include md {
				display: block; }
			@include sm {
				display: none; } } }

	&__desc {
		font-size: 14px;
		line-height: 21px;
		color: $c3;
		margin-bottom: 13px;
		p {
			margin-bottom: 0; } }

	&__link {
		font-weight: 500;
		font-size: 16px;
		line-height: 21px;
		text-decoration-line: underline;
		color: $accent; } }
