.footer-banner {
	background: $c0;
	padding: 16px 0 18px;
	@include lg {
		padding: 25px 0; }

	&__row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include lg {
			flex-wrap: wrap;
			flex-direction: column;
			align-items: center; } }

	&__wrap {
		display: flex;
		align-items: center;
		@include lg {
			flex-wrap: wrap;
			flex-direction: column; } }

	&__phone {
		@include lg {
			margin-bottom: 25px; } }


	&__desc {
		font-weight: 500;
		font-size: 18px;
		line-height: 18px;
		color: #fff;
		@include xl {
			font-size: 16px;
			line-height: 16px; }
		@include lg {
			margin-bottom: 25px;
			text-align: center; }
		@include md {
			font-size: 15px;
			line-height: 18px; }
		@include sm {
			font-weight: normal;
			font-size: 14px;
			line-height: 18px; } }


	&__seperator {
		font-size: 16px;
		line-height: 16px;
		color: $c3;
		margin: 0 20px 0 21px;
		@include xl {
			margin: 0 10px 0 11px; }
		@include lg {
			display: none; } } }
