.check-main {
	background: $c4;
	padding-bottom: 80px;
	@include lg {
		padding-bottom: 40px; }
	@include md {
		padding-bottom: 20px; }
	@include sm {
		padding-bottom: 0px; }
	&__container {
		position: relative;
		@include sm {
			padding: 0; } }
	&__row {
		display: flex;
		margin: 0 -10px;
		@include md {
			justify-content: center; }
		@include sm {
			margin: 0; } }
	&__col {
		padding: 0 10px;
		@include sm {
			padding: 0; }
		&--big {
			max-width: 760px;
			width: 100%;
			@include xl {
				max-width: 620px; }
			@include lg {
				max-width: 380px; }
			@include md {
				display: none; } }
		&--small {
			max-width: 340px;
			width: 100%;
			position: relative;
			@include xl {
				max-width: 330px; }
			@include md {
				max-width: none; } } }
	&__fines-found {
		margin-bottom: 20px;
		@include md {
			margin-top: 15px;
			margin-bottom: 15px; }
		@include sm {
			margin-right: 15px;
			margin-left: 15px; } }
	&__fines-cards {
		margin-bottom: 20px;
		@include sm {
			margin-bottom: 15px; } }
	&__alert {
		@include md {
			margin-bottom: 15px;
			&::after {
				content: '';
				display: none; } }
		@include sm {
			margin-right: 15px;
			width: auto;
			margin-left: 15px; } }

	&__alert-wrap {
		min-height: 110px;
		margin-bottom: 20px;
		position: relative;
		@include xl {
			min-height: 150px; } }
	&__merits {
		padding-bottom: 20px;
		@include sm {
			margin-bottom: 15px; } }
	&__fine {
		&:not(:last-child) {
			margin-bottom: 20px; }
		@include md {
			display: none; } }
	&__show-more {
		@include md {
			display: none; } }
	&__scrolling-wrap {
		position: relative;
		overflow: hidden;
		max-width: 319px;
		width: 100%;
		max-height: 95vh;
		@include xl {
			max-width: 310px; }
		@include md {
			max-height: none;
			overflow: auto;
			max-width: none; }
		&--scroll {
			position: fixed;
			top: 20px; } }
	&__scrolling {
		max-height: 95vh;
		overflow-y: auto;
		overflow-x: hidden;
		@include md {
			max-height: none;
			overflow: auto;
			width: 100% !important;
			max-width: none !important; } } }
.red {
	color: $c1; }
