.main {
	background: $c4;
	padding: 2.5rem 0 3.0625rem;
	// display: none
	@include sm {
		padding-bottom: 0; }

	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px; }

	&__col {
		padding: 0 10px;
		flex-basis: 69.1%;
		max-width: 69.1%;
		&:first-child {
			@include lg {
				margin-bottom: 15px; } }
		@include xl {
			flex-basis: 64.5%;
			max-width: 64.5%; }
		@include lg {
			flex-basis: 100% !important;
			max-width: 100% !important; }

		&:last-child {
			flex-basis: 30.9%;
			max-width: 30.9%;
			@include xl {
				flex-basis: 35.5%;
				max-width: 35.5%; }
			@include lg {
				display: flex;
				justify-content: space-between; } } }

	&__mobile-bottom-content {
		@include sm {
			background: #fff;
			padding: 30px 0;
			border-bottom: 1px solid $c4; } }

	&__top-line {
		background: #fff;
		padding: 40px 85px 48px 82px;
		@include xl {
			padding: 36px 40px 37px 40px; }
		@include md {
			padding: 25px 30px 26px 30px; }
		@include sm {
			padding: 0;
			background: transparent; } }


	&__bottom-line {
		background: #FAFAFB;
		padding: 22px 48px 19px 16px;
		@include xxl {
			padding: 28px 48px 24px 16px; }
		@include xl {
			// padding: 22px 16px 19px 16px
			padding: 30.9px 16px 29px 16px; }
		@include sm {
			display: none; } } }
