.news {
	padding: 4.5rem 0 3.5rem;
	background: $c4;
	@include lg {
		padding: 4rem 0 3rem; }
	@include sm {
		padding: 30px 0 25px; }
	&__title {
		@include lg {
			margin-bottom: 0; }
		&-wrap {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 2.25rem;
			@include sm {
				margin-bottom: 23px; } } }

	&__link {
		line-height: 30px;
		@include sm {
			text-align: center;
			background: #fff;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 5px 10px; } } }

.news {
	&-row {
		margin: 0 -10px;
		display: flex;
		flex-wrap: wrap;
		&--bottom {
			@include min_sm {
				margin-bottom: 11px; } } }
	&-col {
		flex-basis: 33.333%;
		max-width: 33.333%;
		padding: 0 10px;
		margin-bottom: 20px;
		@include xl {
			flex-basis: 50%;
			max-width: 50%; }
		@include sm {
			flex-basis: 100%;
			max-width: 100%; }
		&--full {
			flex-basis: 100%;
			max-width: 100%; } } }
