body {
	min-width: 320px;
	position: relative;
	font-family: $deff;
	line-height: 1.65;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased; }

html,
body {
	font-size: 16px;
	@include xxl {
		font-size: 15px; }
	@include xl {
		font-size: 14px; }
	@include lg {
		font-size: 12px; }
 }	// +sm

.hide-block {
	display: none; }

.page {
	overflow-x: hidden; }

.baron__bar {
	z-index: 3; }

.nowrap {
	white-space: nowrap; }

.fancybox-slide {
	@include sm {
		padding: 15px; } }

.fancybox-thumbs__list a::before {
	border: 2px solid $c1; }

.fancybox-progress {
	background: $c1; }
.owl-nav-style {
	@include lg {
		.owl-prev,
		.owl-next {
			box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15); }
		.owl-prev {
			left: 0 !important;
			transform: translate(-50%, -50%); }
		.owl-next {
			right: 0 !important;
			transform: translate(50%, -50%); } } }
////////// Modules
@import "./_layout-import";
