.banner {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	border: 1px solid #DA9A9A;
	border-radius: 3px;
	// padding: 36px 50px 36px 43px
	padding: 34px 48px 31px 41px;
	@include xl {
		padding: 34px 30px 31px 30px;
		justify-content: space-between;
		align-items: center; }
	@include sm {
		padding: 20px; }


	&__icon-wrap {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 34px;
		margin-top: 12px;
		@include xl {
			margin-top: 0; }
		@include lg {
			flex-basis: 7.6%;
			max-width: 7.6%; }
		@include md {
			margin-right: 20px; }
		svg {
			fill: #DA9B9B;
			width: 48px;
			height: 45px;
			min-width: 48px;
			min-height: 45px;
			@include md {
				width: 40px;
				height: 37px;
				min-width: 40px;
				min-height: 37px; } } }

	&__title {
		font-weight: 500;
		font-size: 20px;
		line-height: 30px;
		color: $c0;
		margin-bottom: 7px;
		@include lg {
			font-size: 18px;
			line-height: 27px; }
		@include md {
			font-size: 17px;
			line-height: 26px; }
		@include sm {
			font-size: 16px;
			line-height: 21px; }
		&-wrap {
			margin-right: 75px;
			@include xl {
				max-width: 550px;
				margin-right: 20px; }
			@include lg {
				margin-right: 0;
				flex-basis: calc( 92.4% - 34px);
				max-width: calc( 92.4% - 34px); }
			@include md {
				margin-right: 0;
				flex-basis: calc( 92.4% - 20px);
				max-width: calc( 92.4% - 20px);
				margin-top: 5px; } } }

	&__desc {
		line-height: 18px;
		color: $c3;
		max-width: 630px;
		@include md {
			margin-top: 20px;
			font-weight: normal; }
		p {
			margin-bottom: 0; } }

	&__btn {
		font-weight: bold;
		font-size: 16px;
		color: #fff;
		padding: 14.5px 41.5px;
		margin-top: 11px;
		@include xl {
			margin-top: 0;
			padding: 14.5px 35px; }
		@include lg {
			flex-basis: 100%;
			max-width: 100%;
			margin-top: 30px; } } }
