.nav {
	&__list {
		display: flex;
		align-items: center;
		@include lg {
			flex-direction: column;
			align-items: flex-start;
			padding-bottom: 7.35vh;
			margin-bottom: 5vh;
			border-bottom: 1px solid $c4; }
		@include sm {
			margin-bottom: 3.5vh;
			// margin-bottom: 4.65vh
			@include h600 {
				margin-bottom: 6vh; }
			@include h700 {
				margin-bottom: 8vh; }
			@include h800 {
				margin-bottom: 10vh; } } }


	&__item {
		@include min_lg {
			padding: 1px 33px;
			border-right: 1px solid #DCDEE0;
			@include xl {
				padding: 1px 25px; } }
		&:first-child {
			@include min_lg {
				padding: 1px 33px 1px 7px;
				@include xl {
					padding: 1px 25px 1px 7px; } } }

		&:last-child {
			border-right: 0; }
		&:not(:last-child) {
			@include lg {
				// margin-bottom: 30px
				margin-bottom: 4.29vh; } } }

	&__link {
		position: relative;
		font-weight: 500;
		font-size: 16px;
		line-height: 17px;
		color: $c0;
		text-align: center;
		@include min_lg {
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				bottom: -16px;
				width: 110%;
				height: 2px;
				background: $accent;
				opacity: 0;
				transition: all 0.3s ease;
				pointer-events: none; } }
		&:hover {
			color: $accent;
			@include min_lg {
				&:after {
					bottom: -16px;
					opacity: 1; } } }

		&--active {
			color: $accent;
			@include min_lg {
				&:after {
					bottom: -16px;
					opacity: 1; } } } } }

.footer-nav {
	@include md {
		width: 100%; }
	&--left {
		@include md {
			margin-bottom: 12px; } }
	&--right {
		@include md {
			margin-bottom: 25px;
			padding-bottom: 25px;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 50%;
				width: 76%;
				height: 1px;
				background: #E8EAEF;
				transform: translateX(-50%); } } }
	&__item {
		@include md {
			display: flex;
			justify-content: center;
			align-items: center; }
		&:not(:last-child) {
			@include md {
				margin-bottom: 12px; } } }

	&__link {
		font-weight: 500;
		font-size: 16px;
		line-height: 16px;
		text-decoration: underline;
		color: $accent;
		@include md {
			text-align: center; }
		&:hover {
			color: $accent;
			text-decoration: none; } } }
