.desc-nav {
	&__list {
		padding-left: 20px;
		@include sm {
			padding-left: 10px; } }

	&__item {}

	&__link {
		font-size: 16px;
		line-height: 27px;
		text-decoration: underline;
		color: $accent;
		@include lg {
			font-size: 15px; }
		@include md {
			font-size: 13px; }
		@include sm {
			font-size: 12px; } }

	&:hover {
		color: #2969EC;
		text-decoration: none; } }
