.sites {
	padding: 4.625rem 0 3.4375rem;
	@include lg {
		padding: 4rem 0 3rem; }
	@include md {
		padding: 3rem 0 2rem; }
	@include sm {
		padding: 30px 0; }


	&__title {
		margin-bottom: 10px;
		@include lg; }

	&__desc {
		max-width: 700px;
		margin-bottom: 38px;
		@include min_xl {
			line-height: 24px; }
		p {
			margin-bottom: 0; } }

	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -12.5px; }

	&__col {
		flex-basis: 50%;
		max-width: 50%;
		padding: 0 12.5px;
		margin-bottom: 25px;
		@include md {
			flex-basis: 100%;
			max-width: 100%; } } }
